/* ==========================================================================
   Portfolio Single page responsive style - begin
   ========================================================================== */

.mkdf-portfolio-single-holder {

    .mkdf-ps-image-holder {

        &.mkdf-ps-gallery-images {

            /***** Columns Layout - begin *****/

            @include laptop-landscape {

                $columns_number: ('four');

                @for $i from 0 to length($columns_number) {
                    &.mkdf-ps-#{nth($columns_number,$i+1)}-columns {
                        $item_size: 100% / 3;

                        .mkdf-ps-image {
                            width: $item_size;
                        }

                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                            .mkdf-ps-image {

                                &:nth-child(3n+1) {
                                    clear: both;
                                }
                            }
                        }
                    }
                }
            }

            @include ipad-landscape {

                $columns_number: ('three', 'four');

                @for $i from 0 to length($columns_number) {
                    &.mkdf-ps-#{nth($columns_number,$i+1)}-columns {

                        .mkdf-ps-image {
                            width: 50%;
                        }

                        @media only screen and (min-width: $phone-landscape-plus-pixel) {

                            .mkdf-ps-image {

                                &:nth-child(2n+1) {
                                    clear: both;
                                }
                            }
                        }
                    }
                }
            }

            @include phone-landscape {

                .mkdf-ps-image {
                    width: 100% !important; // !important is set to override all other stronger selectors
                }
            }

            /***** Columns Layout - end *****/
        }

        &.mkdf-ps-masonry-images {

            @include laptop-landscape {

                $masonry_columns_number: ('four', 'five');

                @for $i from 0 to length($masonry_columns_number) {
                    &.mkdf-ps-#{nth($masonry_columns_number,$i+1)}-columns {
                        $column_width: 100% / 3;

                        .mkdf-ps-image,
                        .mkdf-ps-grid-sizer {
                            width: $column_width;
                        }

                        .mkdf-ps-image.mkdf-masonry-size-large-width,
                        .mkdf-ps-image.mkdf-masonry-size-large-width-height {
                            width: $column_width * 2;
                        }
                    }
                }
            }

            @include ipad-landscape {

                $masonry_columns_number: ('three', 'four', 'five');

                @for $i from 0 to length($masonry_columns_number) {
                    &.mkdf-ps-#{nth($masonry_columns_number,$i+1)}-columns {

                        .mkdf-ps-image,
                        .mkdf-ps-grid-sizer {
                            width: 50%;
                        }

                        .mkdf-ps-image.mkdf-masonry-size-large-width,
                        .mkdf-ps-image.mkdf-masonry-size-large-width-height {
                            width: 100%;
                        }
                    }
                }
            }

            @include phone-landscape {

                $masonry_columns_number: ('two', 'three', 'four');

                @for $i from 0 to length($masonry_columns_number) {
                    &.mkdf-ps-#{nth($masonry_columns_number,$i+1)}-columns {

                        .mkdf-ps-image,
                        .mkdf-ps-grid-sizer {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }

    @include ipad-landscape {

        .mkdf-ps-info-holder {
            margin: 30px 0 0 !important; // can be set inline in option
        }
    }
}

/* ==========================================================================
   Portfolio Single page responsive style - end
   ========================================================================== */